.essay-detail-cont .links {
  width: 100%;
}
.essay-detail-cont .right-content-box {
  width: 100%;
}
.essay-detail-cont .boxs {
  padding: 30px 10%;
}
.essay-detail {
  width: 100%;
}
.essay-detail > p {
  margin-top: 25px;
}
.essay-detail .de-tit {
  margin-top: 0;
  text-align: center;
  font-size: 26px;
  color: #283e53;
  font-weight: bold;
}
.essay-detail .de-explain {
  text-align: right;
  font-size: 14px;
  color: #6c849c;
}
.essay-detail .de-explain > span {
  margin-left: 35px;
}
.essay-detail .de-explain > span:first-child {
  margin-left: 0;
}
.essay-detail .de-explain > span i {
  color: #03acef;
}
.essay-detail .de-stage {
  line-height: 24px;
  font-size: 16px;
  color: #6c849c;
  text-indent: 32px;
}
.essay-detail .de-img {
  text-align: center;
}
.essay-detail .de-img img {
  display: inline-block;
  border-radius: 4px;
  max-width: 100%;
  margin: 0 auto;
}
.essay-detail .de-control {
  margin-top: 25px;
  text-align: right;
  font-size: 14px;
  letter-spacing: 2px;
}
.essay-detail .de-control > a {
  margin-left: 15px;
}
.essay-detail .de-control > a:first-child {
  margin-left: 0;
}
.essay-detail .de-control .meiyong .icon-line-thumb {
  transform: rotate(90deg);
}
.essay-detail .de-control .reportBox {
  margin-left: 10px;
}
.essay-detail .de-control .reportBox .jubao-btn {
  color: #6c849c;
}
.essay-detail .de-control .reportBox .jubao-btn:hover {
  color: #03acef;
}
.essay-detail .de-control .reportBox .reportList {
  left: -38px;
  top: 33px;
}
.discuss-box {
  margin-bottom: 20px;
}
.discuss-box .label-input {
  margin-bottom: 30px;
}
.discuss-box .label-input label {
  margin-bottom: 10px;
  font-size: 14px;
  color: #6c849c;
}
.discuss-box .label-input input[type="text"] {
  display: block;
  padding-bottom: 14px;
  border: none;
  outline: none;
  border-bottom: 1px solid #D4E1E9;
  min-width: 356px;
}
.discuss-box .label-input input[type="text"]::placeholder {
  color: #6C849C;
}
.discuss-box .label-input .area {
  width: 100%;
  height: 70px;
  display: block;
}
.discuss-box .label-input .area::placeholder {
  color: #6C849C;
}
.discuss-box .label-input .necessarily {
  color: #F54545;
}
.discuss-box .btn-box {
  text-align: right;
}
